/*select box*/
.select-box {
    background: #4b4149;
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    color: #fff;
    text-align: center;
    line-height: 4.2;
    transition: .2s;
    font-size: 14px;
    font-weight: bold;
}
.select-box-red {
    background: #de474b;
}
.select-box .cart-link {
    display: inline-block;
    text-decoration: none;
    background: #fff;
    border-radius: 20px;
    padding: 11px 32px;
    line-height: normal;
    color: #000;
    text-transform: uppercase;
    font-size: 14px;
    margin-left: 20px;
    transition: .2s;
    font-weight: normal;
}
.select-box .cart-link:hover {
    box-shadow: inset 0 0 10px 0 rgba(50, 50, 50, 0.75);

}
.select-box .count-in-cart {
    background: #000;
    border-radius: 16px;
    letter-spacing: 2px;
    text-align: center;
    display: inline-block;
    line-height: 1.7em;
    width: 1.7em;
}




/* checkbox*/
.checkbox {
    --border-color: #de474b;
    --background-color: #de474b;
    --check-color: #fff;
}

.checkbox {
    line-height: normal;
}
.checkbox input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    display: none;
}
.checkbox__rect, .checkbox__cross {
    display: block;
    position: relative;
    cursor: pointer;
    margin-bottom: -10px;
}

.checkbox .checkbox__rect {
    margin-right: 10px;
}
.checkbox .checkbox__rect:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    height: 20px;
    border-radius: 2px;
    border: 2px solid var(--border-color);
    transition: .2s;
}
.checkbox .checkbox__cross:before, .checkbox__cross:after {
    content: '';
    position: absolute;
    top: 13px;
    right: 10px;
    width: 2px;
    height: 1px;
    border-radius: 10px;
    background: var(--check-color);
    transition: .2s;
    opacity: 0;
}
.checkbox input:checked + .checkbox__rect:before {
    background: var(--background-color);
}
.checkbox input:checked + .checkbox__rect .checkbox__cross:after {
    opacity: 1;
    top: 9px;
    right: 15px;
    width: 2px;
    height: 6px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.checkbox input:checked + .checkbox__rect .checkbox__cross:before {
    opacity: 1;
    top: 4px;
    right: 9px;
    width: 2px;
    height: 13px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}


