    border-collapse: collapse;
    width: 100%;
    margin-top: 67px;
    line-height: 3.38;
    color: var(--basic-text-color);
}
.goods-table .goods-table__body {
    text-align: right;
}
.goods-table .goods-table__row {
    border-bottom: var(--row-border-color) 1px solid;
}
.goods-table .goods-table__cell {

}
.goods-table .goods-table__header {
    text-align: right;
    font-size: 13px;
    color: var(--basic-text-color);
}
.goods-table .goods-table__header .goods-table__cell-name {
