/* .advantage*/
.advantage .advantage__header {
    margin-top: 70px;
    margin-bottom: 70px;
}
.advantage .advantage__footer {
    text-align: center;
    margin-top: 74px;
}
.advantage .advantage_container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-bottom: solid 1px #c8af72;
    padding-bottom: 90px;
}
.advantage .advantage__arrow {
    position: relative;
}
.advantage .advantage__arrow:after, .advantage__arrow:before {
    top: -1px;
    left: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
}
.advantage .advantage__arrow:before {
    border-top-color: #c8af72;
    margin-left: -24px;
    border-width: 22px;
}
.advantage .advantage__arrow:after {
    border-top-color: #fff;
    margin-left: -23px;
    border-width: 21px;
}
.advantage .advantage__text {
    display: block;
    margin-top: 10px;
    line-height: 1.6;
}
.advantage .advantage__item {
    -webkit-flex: 1 1 33%;
            flex: 1 1 33%;
}
.advantage .advantage__num {
    display: block;
    width: 66px;
    border: solid 1px #c8af72;
    padding: 10px 0;
    text-align: center;
    color: #c8af72;
    font-size: 40px;
    font-weight: bold;
    float: left;
    margin-right: 21px;
}

